.hobby{
	margin-right: 8px;
	margin-bottom: 16px;
	padding: 6px 16px;
	border-radius: 50px;
	background: $raspberry-sunset;
	display: inline-block;
	filter: drop-shadow(0px 3px 5px rgba(0, 0, 0, 0.25));

	
}
.hobby_secondary{
	@extend .hobby;
	background: $peach-fuzz;

	
	@media screen and (min-width: $screen-one) {
		&:hover{
			opacity: 0.2;
		}
	}
}

.hobby_inner{
	@extend %mabryD;
	font-weight: 600;
	color: white;
}

@media screen and (min-width: $screen-one) {
	.hobby{
		margin-right: 10px;
		margin-bottom: 20px;
		padding: 6px 30px;
	}
	.hobby_inner{
		@include mabryC;
	}
}
@media screen and (min-width: $screen-three) {
	.hobby{
		margin-right: 20px;
		margin-bottom: 25px;
		padding: 8px 35px;
	}
	.hobby_inner{
		font-size: 48px;
	}
}

@media screen and (min-width: $screen-four) {
	.hobby{
		margin-bottom: 40px;
		padding: 18px 40px;
	}
	.hobby_inner{
		@include mabryB;
	}
}